




















































































	export default {
		data() {
			return {
				info: '',
				avator:''
			};
		},
		onLoad() {
			var that = this;
			uni.showLoading({
				title: '数据加载中...'
			})
			uni.request({
				url: that.http + 'user/getUserInfo',
				header: that.header,
				method: 'POST',
				data: {
					user_id: uni.getStorageSync('token')
				},
				success: (res) => {
					uni.hideLoading()
					that.info = res.data.data
					// console.log(JSON.stringify(res))
				},
				fail() {
					uni.hideLoading()
					uni.showToast({
						title: '网络延迟，请刷新重试!',
						icon: 'none'
					})
				}
			})
		},
		onShow() {
			var that = this;
			uni.showLoading({
				title: '数据加载中...'
			})
			uni.request({
				url: that.http + 'user/getUserInfo',
				header: that.header,
				method: 'POST',
				data: {
					user_id: uni.getStorageSync('token')
				},
				success: (res) => {
					uni.hideLoading()
					that.info = res.data.data
				},
				fail() {
					uni.hideLoading()
					uni.showToast({
						title: '网络延迟，请刷新重试!',
						icon: 'none'
					})
				}
			})
		},
		methods: {
			// 退出登录
			tui(){
				uni.clearStorageSync()
				uni.reLaunch({
					url:'../loginPages/login/login'
				})
			},
			backTop() {
				uni.navigateBack({
					delta: 1
				})
			},
// 			sz() {
// 				uni.navigateTo({
// 					url: "../userPages/sz/sz?tel="+this.info.link_tel
// 				})
// 			},
			// 上传头像
			uploadHead() {
				var that = this;
				// 选择图片
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album'],
					success: (wj) => {
						uni.showLoading({
							title: '上传中...'
						})
						// 原生压缩
						plus.zip.compressImage({
								src: wj.tempFilePaths[0],
								dst: "_doc/a.jpg",
								overwrite: true,
								width: '270px',
								height: '270px',
								format: 'jpg',
								quality: 100
							},
							function(e) {
								var reader = new plus.io.FileReader();
								reader.onloadend = function(e) {
									var speech = e.target.result; //base64图片 
									// 上传头像
									uni.request({
										url: that.http + 'index/imgupload',
										header: that.header,
										method: 'POST',
										data: {
											file: speech
										},
										success: (res) => {
											if(res.data.code==200){
												that.info.head_src = res.data.path;
												uni.request({
													url:that.http+'user/reHeadImg',
													method:'POST',
													header:that.header,
													data:{
														head_id:res.data.id,
														user_id:uni.getStorageSync('token')
													},
													success: (result) => {
														uni.hideLoading()
														if(result.data.code==200){
															uni.showToast({
																title:'上传成功!',
																icon:'none'
															})
														}
													},
													fail(){
														uni.showToast({
															title:'设置头像失败，请重试!',
															icon:'none'
														})
													}
												})
											}else{
												uni.showToast({
													title:res.data.info,
													icon:'none'
												})
											}
										},
										fail() {
											uni.showToast({
												title: '上传时网络延迟，请刷新后重试!',
												icon: 'none'
											})
										}
									})
								};
								reader.readAsDataURL(e.target);
							},
							function(err) {
								plus.nativeUI.alert('未知错误！', function() {});
							}
						);
					},
					fail() {
						uni.showToast({
							title: '打开相册失败，请重试！',
							icon: 'none'
						})
					}
				})
			}
		}
	}
